<template>
  <div class="page_view">
    <div class="title"></div>


    <div id="myChart4" class="chart-view-4"></div>
  </div>

</template>

<script>
import { getRequest } from "../../utils/api";
var _this;

export default {
  data() {
            return {
                record:[],
                aid:"",
                likes:"",
                title:"",
                cateName:""
            }
        },
  created: function() {
    // `this`  vm 
    _this = this;


  },
  mounted() {
    //,


    _this.showChart4();
  },
  methods: {

    showChart4() {
      var myChart4 = _this.$echarts.init(document.getElementById("myChart4"));

 getRequest("/census/all").then(
        resp => {

          _this.record = resp.data;
          var str="";
        _this.record.forEach(function(e){
            str+=e.title+",";
        });
        str=str.substring(0,str.length-1);
       // _this.$alert(_this.str);
     // _this.$alert(_this.record);

        },
        resp => {
          if (resp.response.status == 403) {
            _this.$message({
              type: "error",
              message: resp.response.data
            });
          }

        }
      );


      var colors = ['#FFAE57', '#FF7853', '#EA5151', '#CC3F57', '#9A2555'];
var bgColor = '#2E2733';

var itemStyle = {
    star5: {
        color: colors[0]
    },
    star4: {
        color: colors[1]
    },
    star3: {
        color: colors[2]
    },
    star2: {
        color: colors[3]
    }
};

var data = [{
    name: '',
    itemStyle: {
        color: colors[1]
    },
    children: [{
        name: '',
        children: [{
            name: '5',
            children: [{
                name: ''
            }, {
                name: ''
            }, {
                name: ''
            }]
        }, {
            name: '4',
            children: [{
                name: ''
            }, {
                name: ''
            }, {
                name: ''
            }]
        }, {
            name: '3',
            children: [{
                name: ''
            }]
        }]
    }, {
        name: '',
        children: [{
            name: '5',
            children: [{
                name: ''
            }]
        }, {
            name: '4',
            children: [{
                name: ''
            }, {
                name: ''
            }]
        }, {
            name: '3',
            children: [{
                name: ''
            }]
        }]
    }]
}, {
    name: '',
    itemStyle: {
        color: colors[2]
    },
    children: [{
        name: '',
        children: [{
            name: '5',
            children: [{
                name: ''
            }]
        }, {
            name: '4',
            children: [{
                name: ''
            }, {
                name: ''
            }]
        }, {
            name: '3',
            children: [{
                name: '\n&RPG'
            }]
        }]
    }, {
        name: '',
        children: [{
            name: '5',
            children: [{
                name: ''
            }]
        }, {
            name: '4',
            children: [{
                name: ''
            }, {
                name: ''
            }, {
                name: '',
            }]
        }, {
            name: '3',
            children: [{
                name: ''
            }]
        }]
    }, {
        name: '',
        children: [{
            name: '5',
            children: [{
                name: ''
            }]
        }, {
            name: '4',
            children: [{
                name: ''
            }, {
                name: ''
            }]
        }, {
            name: '3',
        }, {
            name: '2',
            children: [{
                name: ''
            }]
        }]
    }, {
        name: '',
        children: [{
            name: '4',
            children: [{
                name: ''
            }, {
                name: ''
            }, {
                name: ''
            }]
        }]
    }, {
        name: '',
        children: [{
            name: '5',
            children: [{
                name: ''
            }]
        }, {
            name: '4',
            children: [{
                name: ''
            }, {
                name: ''
            }]
        }, {
            name: '3',
            children: [{
                name: ''
            }]
        }]
    }, {
        name: '',
        children: [{
            name: '4',
            children: [{
                name: ''
            }]
        }]
    }, {
        name: '',
        children: [{
            name: '5',
            children: [{
                name: ''
            }]
        }, {
            name: '4',
            children: [{
                name: 'Three.js '
            }]
        }]
    }]
}];

for (var j = 0; j < data.length; ++j) {
    var level1 = data[j].children;
    for (var i = 0; i < level1.length; ++i) {
        var block = level1[i].children;
        var bookScore = [];
        var bookScoreId;
        for (var star = 0; star < block.length; ++star) {
            var style = (function (name) {
                switch (name) {
                    case '5':
                        bookScoreId = 0;
                        return itemStyle.star5;
                    case '4':
                        bookScoreId = 1;
                        return itemStyle.star4;
                    case '3':
                        bookScoreId = 2;
                        return itemStyle.star3;
                    case '2':
                        bookScoreId = 3;
                        return itemStyle.star2;
                }
            })(block[star].name);

            block[star].label = {
                color: style.color,
                downplay: {
                    opacity: 0.5
                }
            };

            if (block[star].children) {
                style = {
                    opacity: 1,
                    color: style.color
                };
                block[star].children.forEach(function (book) {
                    book.value = 1;
                    book.itemStyle = style;

                    book.label = {
                        color: style.color
                    };

                    var value = 1;
                    if (bookScoreId === 0 || bookScoreId === 3) {
                        value = 5;
                    }

                    if (bookScore[bookScoreId]) {
                        bookScore[bookScoreId].value += value;
                    }
                    else {
                        bookScore[bookScoreId] = {
                            color: colors[bookScoreId],
                            value: value
                        };
                    }
                });
            }
        }

        level1[i].itemStyle = {
            color: data[j].itemStyle.color
        };
    }
}

let option = {
    backgroundColor: bgColor,
    color: colors,
    series: [{
        type: 'sunburst',
        center: ['50%', '48%'],
        data: data,
        sort: function (a, b) {
            if (a.depth === 1) {
                return b.getValue() - a.getValue();
            }
            else {
                return a.dataIndex - b.dataIndex;
            }
        },
        label: {
            rotate: 'radial',
            color: bgColor
        },
        itemStyle: {
            borderColor: bgColor,
            borderWidth: 2
        },
        levels: [{}, {
            r0: 0,
            r: 40,
            label: {
                rotate: 0
            }
        }, {
            r0: 40,
            r: 105
        }, {
            r0: 115,
            r: 140,
            itemStyle: {
                shadowBlur: 2,
                shadowColor: colors[2],
                color: 'transparent'
            },
            label: {
                rotate: 'tangential',
                fontSize: 10,
                color: colors[0]
            }
        }, {
            r0: 140,
            r: 145,
            itemStyle: {
                shadowBlur: 80,
                shadowColor: colors[0]
            },
            label: {
                position: 'outside',
                textShadowBlur: 5,
                textShadowColor: '#333',
            },
            downplay: {
                label: {
                    opacity: 0.5
                }
            }
        }]
    }]
};
      myChart4.setOption(option);
    }
  }
};

</script>

<style>
.page_view {
  padding: 20px 3%;
  text-align: center;
}

.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.chart-view {
  margin: 20px auto;
  width: 400px;
  height: 400px;
}

.chart-view-4 {
  margin: 20px auto;
  width: 800px;
  height: 600px;
}
</style>



