<template>
  <div class="page_view">
    <div class="title"></div>

    <div id="myChart1" class="chart-view"></div>

    <div id="myChart2" class="chart-view"></div>

    <div id="myChart3" class="chart-view"></div>

    <div id="myChart4" class="chart-view-4"></div>
  </div>
  
</template>

<script>
var that;

export default {
  name: "echart",
  data() {
    return {
      chart1_title: "",
      chart1_dataName: "",
      chart1_name: null,
      chart1_data: null
    };
  },
  created: function() {
    // `this`  vm 
    that = this;

    that.chart1_name = ["", "", "", "", "", ""];
    that.chart1_data = [5, 20, 36, 10, 10, 20];
  },
  mounted() {
    //,
    that.showChart1();

    that.showChart2();

    that.showChart3();

    that.showChart4();
  },
  methods: {
    showChart1() {
      // domecharts
      let myChart1 = that.$echarts.init(document.getElementById("myChart1"));
      // 
      myChart1.setOption({
        title: { text: that.chart1_title },
        tooltip: {},
        xAxis: {
          data: that.chart1_name
        },
        yAxis: {},
        series: [
          {
            name: that.chart1_dataName,
            type: "bar",
            data: that.chart1_data
          }
        ]
      });
    },
    showChart2() {
      console.log("showChart2");
      let data2 = [
        { value: 335, name: "" },
        { value: 310, name: "" },
        { value: 234, name: "" },
        { value: 135, name: "" },
        { value: 1548, name: "" }
      ];

      // domecharts
      let myChart2 = that.$echarts.init(document.getElementById("myChart2"));
      // 
      var option2 = {
        title: {
          text: ""
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        series: {
          type: "pie",
          radius: "55%",
          center: ["50%", "60%"],
          data: data2
        }
      };

      myChart2.setOption(option2);
    },
    showChart3() {
      let myChart3 = that.$echarts.init(document.getElementById("myChart3"));
      var option3 = {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          x: "right",
          data: ["", "", "", "", ""]
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["50%", "70%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "14",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              { value: 335, name: "" },
              { value: 310, name: "" },
              { value: 234, name: "" },
              { value: 135, name: "" },
              { value: 1548, name: "" }
            ]
          }
        ]
      };
      myChart3.setOption(option3);
    },
    showChart4() {
      var myChart4 = that.$echarts.init(document.getElementById("myChart4"));
      let option4 = {
    angleAxis: {
    },
    radiusAxis: {
        type: 'category',
        data: ['', '', '', ''],
        z: 10
    },
    polar: {
    },
    series: [{
        type: 'bar',
        data: [1, 2, 3, 4],
        coordinateSystem: 'polar',
        name: 'A',
        stack: 'a'
    }, {
        type: 'bar',
        data: [2, 4, 6, 8],
        coordinateSystem: 'polar',
        name: 'B',
        stack: 'a'
    }, {
        type: 'bar',
        data: [1, 2, 3, 4],
        coordinateSystem: 'polar',
        name: 'C',
        stack: 'a'
    }],
    legend: {
        show: true,
        data: ['A', 'B', 'C']
    }
};
    myChart4.setOption(option4);
    }
  }
};

</script>

<style>
.page_view {
  padding: 20px 3%;
  text-align: center;
}

.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.chart-view {
  margin: 20px auto;
  width: 400px;
  height: 400px;
}

.chart-view-4 {
  margin: 20px auto;
  width: 800px;
  height: 600px;
}
</style>



